/* Responsive Video */

.wb-video-container {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  padding-top: 25px;
  height: 0;
  margin-bottom: 20px;
  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

/* Responsive Image */

.img-responsive {
  max-width: 100%;
}

/* Browser Happy Notice */

.browsehappy {
  margin-bottom: 0;
  text-align: center;
  .alert-link {
    text-decoration: underline;
  }
}

/* Screen Reader Only */
.sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  padding: 0;
  position: absolute;
  margin: -1px;
  overflow: hidden;
  white-space: nowrap; /* added line */
  width: 1px;
}
